.task-list {
  height: 100%;
}

.task-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid #ecf0f1;
}

.task-list-header h3 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.2rem;
}

.task-count {
  background-color: #3498db;
  color: white;
  padding: 0.25rem 0.75rem;
  border-radius: 15px;
  font-size: 0.85rem;
  font-weight: bold;
}

.empty-tasks {
  text-align: center;
  padding: 3rem 1rem;
  color: #7f8c8d;
}

.empty-tasks p {
  margin: 0;
  font-size: 1.1rem;
}

.tasks {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.task-item {
  border: 1px solid #ecf0f1;
  border-radius: 8px;
  padding: 1rem;
  transition: all 0.2s;
  background-color: white;
}

.task-item:hover {
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  transform: translateY(-1px);
}

.task-item.completed {
  opacity: 0.7;
  background-color: #f8f9fa;
}

.task-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 0.5rem;
}

.task-title-section {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
}

.toggle-btn {
  background: none;
  border: 2px solid #27ae60;
  color: #27ae60;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  transition: all 0.2s;
  flex-shrink: 0;
}

.toggle-btn:hover {
  background-color: #27ae60;
  color: white;
}

.task-item.completed .toggle-btn {
  background-color: #27ae60;
  color: white;
}

.task-title {
  margin: 0;
  color: #2c3e50;
  font-size: 1rem;
  font-weight: 600;
}

.task-item.completed .task-title {
  text-decoration: line-through;
  color: #7f8c8d;
}

.task-actions {
  display: flex;
  gap: 0.5rem;
}

.edit-btn, .delete-btn {
  padding: 0.25rem 0.75rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.85rem;
  transition: background-color 0.2s;
}

.edit-btn {
  background-color: #f39c12;
  color: white;
}

.edit-btn:hover {
  background-color: #e67e22;
}

.delete-btn {
  background-color: #e74c3c;
  color: white;
}

.delete-btn:hover {
  background-color: #c0392b;
}

.task-description {
  margin: 0.5rem 0;
  color: #666;
  font-size: 0.9rem;
  line-height: 1.4;
}

.task-meta {
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid #ecf0f1;
}

.task-info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.task-details {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.task-time,
.task-points,
.task-location,
.task-notification {
  color: #7f8c8d;
  font-size: 0.85rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.task-notification.enabled {
  color: #27ae60;
  font-weight: 500;
}

.task-notification.disabled {
  color: #e74c3c;
  font-weight: 500;
}

.task-priority {
  padding: 0.25rem 0.5rem;
  border-radius: 12px;
  font-size: 0.75rem;
  font-weight: bold;
  text-transform: uppercase;
}

.task-priority.priority-high {
  background-color: #e74c3c;
  color: white;
}

.task-priority.priority-medium {
  background-color: #f39c12;
  color: white;
}

.task-priority.priority-low {
  background-color: #95a5a6;
  color: white;
}

/* 모바일 최적화 */
@media (max-width: 768px) {
  .task-list {
    height: auto;
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .task-list-header {
    margin-bottom: 1rem;
    padding-bottom: 0.75rem;
    flex-shrink: 0;
  }

  .task-list-header h3 {
    font-size: 1.1rem;
    line-height: 1.3;
  }

  .task-count {
    padding: 0.2rem 0.6rem;
    font-size: 0.8rem;
  }

  .empty-tasks {
    padding: 2rem 1rem;
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .empty-tasks p {
    font-size: 1rem;
    text-align: center;
    line-height: 1.4;
  }

  .tasks {
    gap: 0.75rem;
    flex: 1;
    overflow-y: auto;
    padding-bottom: 1rem;
  }

  .task-item {
    border-radius: 6px;
    padding: 0.875rem;
    margin: 0;
  }

  .task-header {
    flex-direction: column;
    gap: 0.75rem;
    align-items: stretch;
    margin-bottom: 0.75rem;
  }

  .task-title-section {
    gap: 0.75rem;
  }

  .toggle-btn {
    width: 28px;
    height: 28px;
    font-size: 0.9rem;
    border-width: 2px;
    flex-shrink: 0;
  }

  .task-title {
    font-size: 1rem;
    line-height: 1.3;
  }

  .task-actions {
    justify-content: flex-end;
    gap: 0.75rem;
  }

  .edit-btn, .delete-btn {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
    min-height: 40px;
    border-radius: 6px;
    font-weight: 500;
  }

  .task-description {
    margin: 0.75rem 0;
    font-size: 0.9rem;
    line-height: 1.5;
  }

  .task-meta {
    margin-top: 0.75rem;
    padding-top: 0.75rem;
  }

  .task-info-row {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
    margin-bottom: 0.75rem;
  }

  .task-priority {
    padding: 0.3rem 0.6rem;
    font-size: 0.75rem;
    border-radius: 10px;
  }

  .task-details {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0.5rem;
    width: 100%;
  }

  .task-time,
  .task-points,
  .task-location,
  .task-notification {
    font-size: 0.85rem;
    padding: 0.25rem 0;
  }

  /* 알림 상태는 전체 너비 차지 */
  .task-notification {
    grid-column: 1 / -1;
    justify-self: start;
  }
}

/* 작은 모바일 화면 */
@media (max-width: 480px) {
  .task-list-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
    text-align: left;
  }

  .task-list-header h3 {
    font-size: 1rem;
  }

  .task-count {
    align-self: flex-start;
  }

  .task-item {
    padding: 0.75rem;
  }

  .task-header {
    gap: 0.5rem;
  }

  .task-title {
    font-size: 0.95rem;
  }

  .task-actions {
    gap: 0.5rem;
  }

  .edit-btn, .delete-btn {
    padding: 0.4rem 0.8rem;
    font-size: 0.85rem;
    min-height: 36px;
  }

  .task-details {
    grid-template-columns: 1fr;
    gap: 0.4rem;
  }

  .task-time,
  .task-points,
  .task-location,
  .task-notification {
    font-size: 0.8rem;
  }
}

/* 터치 디바이스에서 호버 효과 조정 */
@media (hover: none) and (max-width: 768px) {
  .task-item:hover {
    transform: none;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  }

  .toggle-btn:hover,
  .edit-btn:hover,
  .delete-btn:hover {
    transform: none;
  }
}