.chatbot-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.chatbot-container {
  background: white;
  border-radius: 15px;
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.chatbot-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #ecf0f1;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border-radius: 15px 15px 0 0;
}

.chatbot-header h3 {
  margin: 0;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.close-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.chatbot-content {
  padding: 2rem;
}

.input-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.instruction {
  text-align: center;
  margin-bottom: 1rem;
}

.instruction p {
  margin: 0.5rem 0;
  color: #2c3e50;
  font-size: 1.1rem;
}

.sub-text {
  color: #7f8c8d !important;
  font-size: 0.9rem !important;
}

.text-input {
  width: 100%;
  min-height: 200px;
  padding: 1rem;
  border: 2px solid #ecf0f1;
  border-radius: 10px;
  font-size: 1rem;
  font-family: inherit;
  resize: vertical;
  transition: border-color 0.3s;
}

.text-input:focus {
  outline: none;
  border-color: #667eea;
}

.text-input::placeholder {
  color: #bdc3c7;
  line-height: 1.4;
}

.error-message {
  background-color: #fee;
  color: #e74c3c;
  padding: 1rem;
  border-radius: 8px;
  border: 1px solid #fadbd8;
  text-align: center;
}

.action-buttons {
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.parse-btn,
.confirm-btn,
.reset-btn {
  padding: 0.75rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  min-width: 120px;
}

.parse-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.parse-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(102, 126, 234, 0.3);
}

.parse-btn:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.confirm-btn {
  background: linear-gradient(135deg, #56ab2f 0%, #a8e6cf 100%);
  color: white;
}

.confirm-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(86, 171, 47, 0.3);
}

.confirm-btn:disabled,
.reset-btn:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.reset-btn {
  background: linear-gradient(135deg, #f39c12 0%, #f1c40f 100%);
  color: white;
}

.reset-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(243, 156, 18, 0.3);
}

.result-section {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.success-message {
  background: linear-gradient(135deg, #56ab2f 0%, #a8e6cf 100%);
  color: white;
  padding: 1rem;
  border-radius: 10px;
  text-align: center;
  font-weight: 600;
  font-size: 1.1rem;
}

.parsed-info {
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid #e9ecef;
}

.parsed-info h4 {
  margin: 0 0 1rem 0;
  color: #2c3e50;
  font-size: 1.1rem;
}

.parsed-info ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.parsed-info li {
  padding: 0.5rem 0;
  border-bottom: 1px solid #e9ecef;
  color: #34495e;
}

.parsed-info li:last-child {
  border-bottom: none;
}

.original-text {
  background-color: #f8f9fa;
  padding: 1.5rem;
  border-radius: 10px;
  border: 1px solid #e9ecef;
}

.original-text h4 {
  margin: 0 0 1rem 0;
  color: #2c3e50;
  font-size: 1.1rem;
}

.original-content {
  background: white;
  padding: 1rem;
  border-radius: 5px;
  border: 1px solid #dee2e6;
  max-height: 150px;
  overflow-y: auto;
  white-space: pre-wrap;
  color: #6c757d;
  font-size: 0.9rem;
  line-height: 1.4;
}

.success-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
  padding: 3rem 2rem;
  text-align: center;
}

.success-message-large {
  background: linear-gradient(135deg, #56ab2f 0%, #a8e6cf 100%);
  color: white;
  padding: 2rem;
  border-radius: 15px;
  font-size: 1.3rem;
  font-weight: 600;
  box-shadow: 0 10px 30px rgba(86, 171, 47, 0.3);
  animation: successPulse 2s ease-in-out;
}

.auto-close-text {
  color: #7f8c8d;
  font-size: 1rem;
  margin: 0;
}

@keyframes successPulse {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* 모바일 최적화 */
@media (max-width: 768px) {
  .chatbot-overlay {
    padding: 0;
    align-items: flex-end;
  }
  
  .chatbot-container {
    max-height: 90vh;
    border-radius: 20px 20px 0 0;
    width: 100%;
    max-width: none;
    animation: slideUpChatbot 0.3s ease-out;
  }

  @keyframes slideUpChatbot {
    from {
      transform: translateY(100%);
    }
    to {
      transform: translateY(0);
    }
  }
  
  .chatbot-header {
    padding: 1.25rem 1.25rem 1rem;
    border-radius: 20px 20px 0 0;
    position: relative;
  }

  .chatbot-header::before {
    content: '';
    position: absolute;
    top: 0.75rem;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 4px;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 2px;
  }
  
  .chatbot-header h3 {
    font-size: 1.2rem;
    text-align: center;
  }

  .close-btn {
    position: absolute;
    top: 1.25rem;
    right: 1.25rem;
    font-size: 1.25rem;
  }
  
  .chatbot-content {
    padding: 1.25rem;
    overflow-y: auto;
    flex: 1;
  }

  .input-section,
  .result-section {
    gap: 1.25rem;
  }

  .instruction {
    margin-bottom: 1rem;
    text-align: center;
  }

  .instruction p {
    font-size: 1rem;
    margin: 0.5rem 0;
    line-height: 1.4;
  }

  .sub-text {
    font-size: 0.85rem !important;
  }
  
  .text-input {
    min-height: 150px;
    padding: 1rem;
    font-size: 1rem;
    border-radius: 12px;
    border-width: 1px;
  }

  .text-input:focus {
    border-color: #667eea;
    box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
  }

  .error-message {
    padding: 1rem;
    border-radius: 10px;
    font-size: 0.95rem;
    line-height: 1.4;
  }
  
  .action-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .parse-btn,
  .confirm-btn,
  .reset-btn {
    width: 100%;
    min-width: auto;
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
    min-height: 48px;
    border-radius: 10px;
  }

  .success-message {
    padding: 1.25rem;
    border-radius: 12px;
    font-size: 1rem;
    line-height: 1.4;
  }

  .parsed-info,
  .original-text {
    padding: 1.25rem;
    border-radius: 12px;
  }

  .parsed-info h4,
  .original-text h4 {
    font-size: 1rem;
    margin-bottom: 1rem;
  }

  .parsed-info li {
    padding: 0.75rem 0;
    font-size: 0.95rem;
    line-height: 1.4;
  }

  .original-content {
    padding: 1rem;
    border-radius: 8px;
    max-height: 120px;
    font-size: 0.85rem;
  }

  .success-section {
    padding: 2rem 1rem;
    gap: 1.25rem;
  }

  .success-message-large {
    padding: 1.5rem;
    font-size: 1.1rem;
    border-radius: 12px;
  }

  .auto-close-text {
    font-size: 0.95rem;
  }
}

/* 작은 모바일 화면 */
@media (max-width: 480px) {
  .chatbot-container {
    max-height: 95vh;
  }

  .chatbot-header {
    padding: 1rem 1rem 0.75rem;
  }

  .chatbot-header h3 {
    font-size: 1.1rem;
  }

  .close-btn {
    top: 1rem;
    right: 1rem;
  }

  .chatbot-content {
    padding: 1rem;
  }

  .instruction p {
    font-size: 0.95rem;
  }

  .sub-text {
    font-size: 0.8rem !important;
  }

  .text-input {
    min-height: 120px;
    padding: 0.875rem;
    font-size: 0.95rem;
  }

  .action-buttons {
    gap: 0.5rem;
  }

  .parse-btn,
  .confirm-btn,
  .reset-btn {
    padding: 0.75rem 1rem;
    font-size: 0.95rem;
    min-height: 44px;
  }

  .parsed-info,
  .original-text {
    padding: 1rem;
  }

  .parsed-info h4,
  .original-text h4 {
    font-size: 0.95rem;
  }

  .parsed-info li {
    font-size: 0.9rem;
    padding: 0.5rem 0;
  }

  .original-content {
    font-size: 0.8rem;
    max-height: 100px;
  }

  .success-section {
    padding: 1.5rem 0.75rem;
  }

  .success-message-large {
    padding: 1.25rem;
    font-size: 1rem;
  }

  .auto-close-text {
    font-size: 0.9rem;
  }
}

/* 터치 디바이스 최적화 */
@media (hover: none) and (max-width: 768px) {
  .close-btn:hover {
    background-color: transparent;
  }

  .parse-btn:hover:not(:disabled),
  .confirm-btn:hover:not(:disabled),
  .reset-btn:hover:not(:disabled) {
    transform: none;
    box-shadow: none;
  }

  /* iOS Safari 줌 방지 */
  .text-input {
    font-size: 16px;
  }
}