.calendar {
  width: 100%;
}

.calendar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.calendar-header h2 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.5rem;
}

.nav-btn {
  background: none;
  border: 2px solid #3498db;
  color: #3498db;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  cursor: pointer;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s;
}

.nav-btn:hover {
  background-color: #3498db;
  color: white;
}

.calendar-weekdays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1px;
  margin-bottom: 1rem;
}

.weekday {
  padding: 0.75rem;
  text-align: center;
  font-weight: bold;
  color: #7f8c8d;
  background-color: #ecf0f1;
  border-radius: 5px;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 1px;
  background-color: #bdc3c7;
  border-radius: 5px;
  overflow: hidden;
}

.calendar-day {
  background-color: white;
  min-height: 100px;
  padding: 0.5rem;
  cursor: pointer;
  transition: background-color 0.2s;
  position: relative;
  display: flex;
  flex-direction: column;
}

.calendar-day:hover {
  background-color: #f8f9fa;
}

.calendar-day.empty {
  background-color: #ecf0f1;
  cursor: default;
}

.calendar-day.selected {
  background-color: #3498db;
  color: white;
}

.calendar-day.today {
  border: 2px solid #e74c3c;
}

.calendar-day.today.selected {
  border: 2px solid #c0392b;
}

.day-number {
  font-weight: bold;
  font-size: 1rem;
  margin-bottom: 0.25rem;
}

.task-indicators {
  display: flex;
  flex-direction: column;
  gap: 2px;
  flex: 1;
}

.task-indicator {
  height: 4px;
  border-radius: 2px;
  font-size: 0.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.task-indicator.priority-high {
  background-color: #e74c3c;
}

.task-indicator.priority-medium {
  background-color: #f39c12;
}

.task-indicator.priority-low {
  background-color: #95a5a6;
}

.task-indicator.completed {
  opacity: 0.5;
}

.task-indicator.more {
  background-color: #34495e;
  color: white;
  height: 16px;
  font-size: 0.6rem;
  margin-top: 2px;
}

/* 모바일 최적화 */
@media (max-width: 768px) {
  .calendar {
    width: 100%;
    overflow-x: auto;
  }

  .calendar-header {
    margin-bottom: 1rem;
    padding: 0 0.25rem;
  }

  .calendar-header h2 {
    font-size: 1.2rem;
    flex: 1;
    text-align: center;
  }

  .nav-btn {
    width: 44px; /* 최소 터치 영역 */
    height: 44px;
    font-size: 1.1rem;
    border-width: 1px;
    flex-shrink: 0;
  }

  .calendar-weekdays {
    gap: 0;
    margin-bottom: 0.5rem;
  }

  .weekday {
    padding: 0.5rem 0.25rem;
    font-size: 0.8rem;
    border-radius: 0;
  }

  .calendar-grid {
    gap: 0;
    border-radius: 8px;
  }

  .calendar-day {
    min-height: 70px;
    padding: 0.25rem;
    font-size: 0.85rem;
  }

  .calendar-day:first-child {
    border-top-left-radius: 8px;
  }

  .calendar-day:nth-child(7) {
    border-top-right-radius: 8px;
  }

  .calendar-day:nth-last-child(-n+7):first-child,
  .calendar-day:nth-last-child(-n+7):nth-child(7n) {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }

  .day-number {
    font-size: 0.85rem;
    margin-bottom: 0.15rem;
  }

  .task-indicators {
    gap: 1px;
  }

  .task-indicator {
    height: 3px;
    border-radius: 1.5px;
  }

  .task-indicator.more {
    height: 12px;
    font-size: 0.55rem;
    border-radius: 2px;
    margin-top: 1px;
  }

  /* 터치 디바이스에서 호버 효과 제거 */
  @media (hover: none) {
    .calendar-day:hover {
      background-color: white;
    }
    
    .calendar-day.selected:hover {
      background-color: #3498db;
    }

    .nav-btn:hover {
      background: none;
      color: #3498db;
    }
  }
}

/* 작은 모바일 화면 */
@media (max-width: 480px) {
  .calendar-header {
    margin-bottom: 0.75rem;
  }

  .calendar-header h2 {
    font-size: 1.1rem;
  }

  .nav-btn {
    width: 40px;
    height: 40px;
    font-size: 1rem;
  }

  .weekday {
    padding: 0.4rem 0.15rem;
    font-size: 0.75rem;
  }

  .calendar-day {
    min-height: 60px;
    padding: 0.2rem;
  }

  .day-number {
    font-size: 0.8rem;
  }

  .task-indicator {
    height: 2px;
  }

  .task-indicator.more {
    height: 10px;
    font-size: 0.5rem;
  }
}

/* 가로 모드 대응 */
@media (max-width: 768px) and (orientation: landscape) {
  .calendar-day {
    min-height: 50px;
  }

  .task-indicators {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1px;
  }

  .task-indicator {
    width: 8px;
    height: 8px;
    border-radius: 50%;
  }

  .task-indicator.more {
    width: auto;
    height: 8px;
    padding: 0 4px;
    border-radius: 4px;
    font-size: 0.5rem;
  }
}