.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-content {
  background: white;
  border-radius: 10px;
  width: 100%;
  max-width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 1rem;
  border-bottom: 1px solid #ecf0f1;
}

.modal-header h3 {
  margin: 0;
  color: #2c3e50;
  font-size: 1.3rem;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #7f8c8d;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 4px;
  transition: color 0.2s;
}

.close-btn:hover {
  color: #e74c3c;
}

.task-form {
  padding: 1.5rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #2c3e50;
  font-weight: 600;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid #ecf0f1;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s;
  font-family: inherit;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #3498db;
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.form-actions {
  display: flex;
  gap: 1rem;
  justify-content: flex-end;
  margin-top: 2rem;
  padding-top: 1rem;
  border-top: 1px solid #ecf0f1;
}

.cancel-btn,
.save-btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
  font-weight: 600;
}

.cancel-btn {
  background-color: #95a5a6;
  color: white;
}

.cancel-btn:hover {
  background-color: #7f8c8d;
}

.save-btn {
  background-color: #3498db;
  color: white;
}

.save-btn:hover {
  background-color: #2980b9;
}

.notification-toggle {
  background-color: #f8f9fa;
  padding: 1rem;
  border-radius: 8px;
  border: 2px solid #ecf0f1;
}

.notification-label {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 0.5rem !important;
  font-weight: 500 !important;
}

.notification-label input[type="checkbox"] {
  opacity: 0;
  position: absolute;
  width: 0;
  height: 0;
}

.checkbox-custom {
  width: 20px;
  height: 20px;
  border: 2px solid #bdc3c7;
  border-radius: 4px;
  margin-right: 0.75rem;
  position: relative;
  transition: all 0.3s;
  background-color: white;
}

.notification-label input[type="checkbox"]:checked + .checkbox-custom {
  background-color: #27ae60;
  border-color: #27ae60;
}

.notification-label input[type="checkbox"]:checked + .checkbox-custom::after {
  content: '✓';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-weight: bold;
  font-size: 12px;
}

.notification-text {
  font-size: 1rem;
  color: #2c3e50;
}

.notification-description {
  margin: 0;
  font-size: 0.85rem;
  color: #7f8c8d;
  margin-left: 2.75rem;
}

/* 모바일 최적화 */
@media (max-width: 768px) {
  .modal-overlay {
    padding: 0;
    align-items: flex-end;
  }
  
  .modal-content {
    max-height: 90vh;
    border-radius: 20px 20px 0 0;
    margin: 0;
    width: 100%;
    max-width: none;
    animation: slideUp 0.3s ease-out;
  }

  @keyframes slideUp {
    from {
      transform: translateY(100%);
    }
    to {
      transform: translateY(0);
    }
  }
  
  .modal-header {
    padding: 1.25rem 1.25rem 1rem;
    border-radius: 20px 20px 0 0;
    position: relative;
  }

  .modal-header::before {
    content: '';
    position: absolute;
    top: 0.75rem;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 4px;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 2px;
  }
  
  .modal-header h3 {
    font-size: 1.2rem;
    text-align: center;
  }

  .close-btn {
    position: absolute;
    top: 1.25rem;
    right: 1.25rem;
    font-size: 1.25rem;
  }
  
  .task-form {
    padding: 1.25rem;
    overflow-y: auto;
    flex: 1;
  }
  
  .form-group {
    margin-bottom: 1.25rem;
  }

  .form-group label {
    font-size: 0.95rem;
    margin-bottom: 0.5rem;
  }

  .form-group input,
  .form-group textarea,
  .form-group select {
    padding: 0.875rem;
    font-size: 1rem;
    border-radius: 8px;
    border: 1px solid #ddd;
    -webkit-appearance: none;
    appearance: none;
  }

  .form-group input:focus,
  .form-group textarea:focus,
  .form-group select:focus {
    border-color: #3498db;
    box-shadow: 0 0 0 3px rgba(52, 152, 219, 0.1);
  }

  .form-group textarea {
    min-height: 100px;
    resize: vertical;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 1.25rem;
  }

  .notification-toggle {
    padding: 1rem;
    border-radius: 10px;
    margin-bottom: 0.5rem;
  }

  .notification-label {
    margin-bottom: 0.75rem !important;
  }

  .checkbox-custom {
    width: 24px;
    height: 24px;
    margin-right: 1rem;
  }

  .notification-text {
    font-size: 1rem;
    line-height: 1.4;
  }

  .notification-description {
    margin-left: 3rem;
    font-size: 0.85rem;
    line-height: 1.4;
  }
  
  .form-actions {
    flex-direction: row;
    gap: 1rem;
    margin-top: 1.5rem;
    padding-top: 1.25rem;
    position: sticky;
    bottom: 0;
    background: white;
    border-top: 1px solid #ecf0f1;
  }
  
  .cancel-btn,
  .save-btn {
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
    min-height: 48px;
    border-radius: 8px;
    flex: 1;
    font-weight: 600;
  }
}

/* 작은 모바일 화면 */
@media (max-width: 480px) {
  .modal-content {
    max-height: 95vh;
  }

  .modal-header {
    padding: 1rem 1rem 0.75rem;
  }

  .modal-header h3 {
    font-size: 1.1rem;
  }

  .close-btn {
    top: 1rem;
    right: 1rem;
  }

  .task-form {
    padding: 1rem;
  }

  .form-group {
    margin-bottom: 1rem;
  }

  .form-group input,
  .form-group textarea,
  .form-group select {
    padding: 0.75rem;
    font-size: 0.95rem;
  }

  .notification-toggle {
    padding: 0.875rem;
  }

  .checkbox-custom {
    width: 20px;
    height: 20px;
    margin-right: 0.75rem;
  }

  .notification-text {
    font-size: 0.95rem;
  }

  .notification-description {
    margin-left: 2.5rem;
    font-size: 0.8rem;
  }

  .form-actions {
    flex-direction: column;
    gap: 0.75rem;
    margin-top: 1.25rem;
    padding-top: 1rem;
  }

  .cancel-btn,
  .save-btn {
    padding: 0.75rem 1rem;
    font-size: 0.95rem;
    min-height: 44px;
  }
}

/* 터치 디바이스 최적화 */
@media (hover: none) and (max-width: 768px) {
  .close-btn:hover {
    color: #7f8c8d;
  }

  .cancel-btn:hover,
  .save-btn:hover {
    transform: none;
  }

  /* iOS Safari 줌 방지 */
  .form-group input,
  .form-group textarea,
  .form-group select {
    font-size: 16px;
  }
}