.notification-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.notification-container {
  background: white;
  border-radius: 15px;
  width: 100%;
  max-width: 600px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.notification-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 2rem;
  border-bottom: 1px solid #ecf0f1;
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  border-radius: 15px 15px 0 0;
}

.notification-header h3 {
  margin: 0;
  font-size: 1.3rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.close-btn {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: white;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.close-btn:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

.notification-content {
  padding: 2rem;
}

.unsupported-section {
  text-align: center;
  padding: 2rem 0;
}

.warning-message {
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  padding: 1rem;
  border-radius: 10px;
  font-weight: 600;
  margin-bottom: 1rem;
}

.permission-section {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #ecf0f1;
}

.permission-section h4 {
  margin: 0 0 1rem 0;
  color: #2c3e50;
  font-size: 1.1rem;
}

.permission-status {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 1rem;
  background-color: #f8f9fa;
  border-radius: 8px;
  margin-bottom: 1rem;
}

.status-icon {
  font-size: 1.2rem;
}

.status-text {
  font-weight: 600;
  font-size: 1rem;
}

.permission-actions {
  margin-top: 1rem;
}

.permission-description {
  color: #666;
  margin-bottom: 1rem;
  line-height: 1.5;
}

.permission-btn {
  background: linear-gradient(135deg, #e74c3c 0%, #c0392b 100%);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.permission-btn:hover {
  background: linear-gradient(135deg, #d32f2f 0%, #b71c1c 100%);
  transform: translateY(-1px);
  box-shadow: 0 4px 15px rgba(231, 76, 60, 0.3);
}

.permission-granted {
  background: linear-gradient(135deg, #27ae60 0%, #2ecc71 100%);
  color: white;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
}

.success-text {
  margin: 0 0 1rem 0;
  font-weight: 500;
}

.test-notification-btn {
  background: rgba(255, 255, 255, 0.2);
  color: white;
  border: 1px solid rgba(255, 255, 255, 0.3);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s;
}

.test-notification-btn:hover {
  background: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
}

.notification-info {
  margin-bottom: 2rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid #ecf0f1;
}

.notification-info h4 {
  margin: 0 0 1rem 0;
  color: #2c3e50;
  font-size: 1.1rem;
}

.notification-info ul {
  margin: 0;
  padding-left: 1.5rem;
  color: #666;
}

.notification-info li {
  margin-bottom: 0.5rem;
  line-height: 1.4;
}

.scheduled-section h4 {
  margin: 0 0 1rem 0;
  color: #2c3e50;
  font-size: 1.1rem;
}

.scheduled-list {
  max-height: 200px;
  overflow-y: auto;
}

.scheduled-item {
  background-color: #f8f9fa;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 0.75rem;
  border-left: 4px solid #e74c3c;
}

.scheduled-task {
  font-weight: 600;
  color: #2c3e50;
  margin-bottom: 0.25rem;
}

.scheduled-time {
  font-size: 0.9rem;
  color: #7f8c8d;
}

.more-notifications {
  text-align: center;
  color: #7f8c8d;
  font-style: italic;
  padding: 1rem;
}

.no-notifications {
  text-align: center;
  color: #7f8c8d;
  padding: 2rem;
}

.no-notifications p {
  margin: 0.5rem 0;
}

/* 모바일 최적화 */
@media (max-width: 768px) {
  .notification-overlay {
    padding: 0;
    align-items: flex-end;
  }
  
  .notification-container {
    max-height: 90vh;
    border-radius: 20px 20px 0 0;
    width: 100%;
    max-width: none;
    animation: slideUpNotification 0.3s ease-out;
  }

  @keyframes slideUpNotification {
    from {
      transform: translateY(100%);
    }
    to {
      transform: translateY(0);
    }
  }
  
  .notification-header {
    padding: 1.25rem 1.25rem 1rem;
    border-radius: 20px 20px 0 0;
    position: relative;
  }

  .notification-header::before {
    content: '';
    position: absolute;
    top: 0.75rem;
    left: 50%;
    transform: translateX(-50%);
    width: 40px;
    height: 4px;
    background-color: rgba(255, 255, 255, 0.3);
    border-radius: 2px;
  }
  
  .notification-header h3 {
    font-size: 1.2rem;
    text-align: center;
  }

  .close-btn {
    position: absolute;
    top: 1.25rem;
    right: 1.25rem;
    font-size: 1.25rem;
  }
  
  .notification-content {
    padding: 1.25rem;
    overflow-y: auto;
    flex: 1;
  }

  .permission-section,
  .notification-info,
  .scheduled-section {
    margin-bottom: 1.5rem;
    padding-bottom: 1.5rem;
  }

  .permission-section h4,
  .notification-info h4,
  .scheduled-section h4 {
    font-size: 1.1rem;
    margin-bottom: 1rem;
  }
  
  .permission-status {
    flex-direction: row;
    text-align: left;
    gap: 1rem;
    padding: 1rem;
    margin-bottom: 1rem;
  }

  .status-icon {
    font-size: 1.5rem;
  }

  .status-text {
    font-size: 1.1rem;
  }

  .permission-description {
    font-size: 1rem;
    line-height: 1.5;
    margin-bottom: 1rem;
  }

  .permission-btn,
  .test-notification-btn {
    padding: 0.875rem 1.5rem;
    font-size: 1rem;
    min-height: 48px;
    border-radius: 8px;
    display: block;
    width: 100%;
    margin-bottom: 0.75rem;
  }

  .permission-granted {
    padding: 1.25rem;
    border-radius: 12px;
  }

  .success-text {
    font-size: 1rem;
    line-height: 1.4;
    margin-bottom: 1rem;
  }

  .notification-info ul {
    padding-left: 1.25rem;
  }

  .notification-info li {
    font-size: 0.95rem;
    line-height: 1.5;
    margin-bottom: 0.75rem;
  }

  .scheduled-list {
    max-height: 250px;
  }

  .scheduled-item {
    padding: 1rem;
    margin-bottom: 0.75rem;
    border-radius: 10px;
    border-left-width: 4px;
  }

  .scheduled-task {
    font-size: 1rem;
    margin-bottom: 0.5rem;
    line-height: 1.3;
  }

  .scheduled-time {
    font-size: 0.9rem;
  }

  .more-notifications,
  .no-notifications {
    padding: 1.5rem;
    font-size: 1rem;
    line-height: 1.4;
  }

  .unsupported-section {
    padding: 2rem 1rem;
  }

  .warning-message {
    padding: 1.25rem;
    font-size: 1rem;
    line-height: 1.4;
    border-radius: 12px;
  }
}

/* 작은 모바일 화면 */
@media (max-width: 480px) {
  .notification-container {
    max-height: 95vh;
  }

  .notification-header {
    padding: 1rem 1rem 0.75rem;
  }

  .notification-header h3 {
    font-size: 1.1rem;
  }

  .close-btn {
    top: 1rem;
    right: 1rem;
  }

  .notification-content {
    padding: 1rem;
  }

  .permission-section,
  .notification-info,
  .scheduled-section {
    margin-bottom: 1.25rem;
    padding-bottom: 1.25rem;
  }

  .permission-section h4,
  .notification-info h4,
  .scheduled-section h4 {
    font-size: 1rem;
  }

  .permission-status {
    flex-direction: column;
    text-align: center;
    gap: 0.75rem;
  }

  .status-icon {
    font-size: 1.3rem;
  }

  .status-text {
    font-size: 1rem;
  }

  .permission-btn,
  .test-notification-btn {
    padding: 0.75rem 1rem;
    font-size: 0.95rem;
    min-height: 44px;
  }

  .permission-granted {
    padding: 1rem;
  }

  .success-text {
    font-size: 0.95rem;
  }

  .notification-info li {
    font-size: 0.9rem;
    margin-bottom: 0.5rem;
  }

  .scheduled-item {
    padding: 0.875rem;
  }

  .scheduled-task {
    font-size: 0.95rem;
  }

  .scheduled-time {
    font-size: 0.85rem;
  }
}

/* 터치 디바이스 최적화 */
@media (hover: none) and (max-width: 768px) {
  .close-btn:hover {
    background-color: transparent;
  }

  .permission-btn:hover,
  .test-notification-btn:hover {
    transform: none;
  }
}